export default {
	data() {
		return {
			currentTime: '', //当前播放时间
			durationTime: '', //总时长
			current: '', //slider当前进度
			loading: false, //是否处于读取状态
			paused: true, //是否处于暂停状态
			seek: false //是否处于拖动状态
		}
	},
	props: {
		coverurl: '',
		singers: '',
		songname: '',
		src: String, //音频链接
