			type:Boolean,
			default:true
		}, //是否需要上一曲/下一曲按钮
		continue:Boolean,//播放完成后是否继续播放下一首，需定义@next事件
		color: {
			type:String,
			default:'#169af3'
		} //主色调
	},
}
